import React from 'react'
import Link from 'gatsby-link'
import Helmet from 'react-helmet'

const IndexPage = () => (
  
  <div className="page row index-page">
      <Helmet
      title="Kacper Cieciński Junior Web Developer"
      meta={[
        { name: 'description', content: 'Hi I am Kacper Ciecinski' },
        { name: 'keywords', content: 'Kacper, Cieciński, Kacper Cieciński, Ciecinski, developer, junior developer,junior react developer, junior front-end developer,front-end,frontend,backend,junior backend developer,junior rails developer,React,Ruby on Rails,Javascript, HTML5, CSS, webdeveloper, programming enthusiast' },
        ]}
    />
    <div className="col-md-2"></div>
    <div className="col-md-4">
    <img src={require("../me.jpg")} alt=""/>
    </div>
    <div className="col-md-4">
      <div className="info-index-page">
        <h2>Kacper Cieciński</h2>
        <p>frontend developer</p>
        <p>backend developer</p>
        <p>programming enthusiast</p>
        <p> <span>EMAIL:</span>    ciecinski.kacper@gmail.com</p> 
          <a href="https://www.facebook.com/kacper.ciecinski" className="social-front"> <i className="fa fa-facebook-square"></i></a>
          <a href="https://www.twitter.com/kciecinski" className="social-front"> <i className="fa fa-twitter-square"></i></a>
          <a href="https://www.linkedin.com/in/kacper-ciecinski-506b32148/"className="social-front"> <i className="fa fa-linkedin-square"></i></a>
      </div>
      </div>
      <div className="col-md-2"></div>
  </div>
)

export default IndexPage



